import * as React from 'react'
import { observer } from 'mobx-react'
import { observable } from 'mobx'
import * as styles from './main.scss'
@observer
class Main extends React.Component {
  @observable bottom = 0
  @observable left = 0

  render() {
    return (
      <div className={styles.main}>
        hello world!
      </div>
    )
  }
}

export default Main



